import React from 'react';

const Hero = () => {
  return (
    <section id="Hero"
      className="h-screen flex items-center justify-center bg-cover bg-center relative pt-28"  // Adjust pt-28 to your needs
      style={{ backgroundImage: `url('/path-to-hero-image.jpg')` }}
    >
      <div className="absolute inset-0 opacity-50"></div> {/* Dark overlay */}

      <div className="text-center text-black relative z-8">
        <div className="flex items-center justify-center">
          <img
            src={`${process.env.PUBLIC_URL}/logo_dark.png`}
            alt="Logo"
            className="h-40 w-auto" // Fixed height and width
          />
        </div>

        <h1 className="text-5xl font-bold mb-4">Welcome to Digidit</h1>
        <p className="text-xl mb-6 py-6 px-10">We help businesses and individuals create impactful content<br></br>
        Whether it’s reels, TikTok, YouTube videos, or business promos, we ensure quality, care, and timely delivery</p>
        <button
          className="px-6 py-3 bg-yellow-400 text-gray-900 font-bold rounded-lg hover:bg-yellow-500"
          onClick={() => document.getElementById('portfolio').scrollIntoView({ behavior: 'smooth' })}
        >
          Explore My Work
        </button>
      </div>
    </section>
  );
};

export default Hero;
