import React, { useState, useRef } from "react";

const Portfolio = () => {
  const [activeVideo, setActiveVideo] = useState(null); // Track currently playing video
  const videoRefs = useRef([]); // Store references to all video elements

  const handleVideoPlay = (index) => {
    // Pause all other videos when one is playing
    videoRefs.current.forEach((video, i) => {
      if (i !== index && video) {
        video.pause();
      }
    });

    // Set the active video
    setActiveVideo(index);

    // Enable audio for the current video
    if (videoRefs.current[index]) {
      videoRefs.current[index].muted = false;
    }
  };

  const handleVideoPause = () => {
    setActiveVideo(null);
  };

  const projects = [
    { title: "Project 1", video: "/video1.mp4" },
    { title: "Project 2", video: "/video5.mp4" },
    { title: "Project 3", video: "/video4.mp4" },
    { title: "Project 4", video: "/video6.mp4" },
  ];

  return (
    <section id="portfolio" className="py-10 px-4 md:px-10 lg:px-20">
      <div className="container mx-auto text-center">
        <h1 className="text-3xl md:text-4xl font-bold mb-8">Portfolio</h1>

        {/* Projects Grid */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
          {projects.map((project, index) => (
            <div key={index} className="shadow-lg rounded-lg overflow-hidden">
              <div className="relative">
                <video
                  ref={(el) => (videoRefs.current[index] = el)} // Reference each video
                  loop
                  controls
                  muted
                  onPlay={() => handleVideoPlay(index)} // Handle play event
                  onPause={handleVideoPause} // Handle pause event
                  className="w-full h-auto object-cover"
                >
                  <source src={project.video} type="video/mp4" />
                  Your browser does not support the video tag.
                </video>
              </div>
            </div>
          ))}
        </div>

        {/* Other sections (e.g., Viral Videos, Podcast) remain the same */}
         {/* Viral Videos Section */}
<div className="mt-16">
  <h2 className="text-2xl md:text-3xl font-bold mb-6">Videos That Go Viral!</h2>
  <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
    {/* Video 1 */}
    <div className="relative overflow-hidden rounded-lg shadow-lg">
      <a
        href="https://youtube.com/shorts/L_4a8CcQHo0?si=1DTMHzKVOZtUAbW4"
        target="_blank"
        rel="noopener noreferrer"
        className="block"
      >
        <div className="rounded-lg overflow-hidden">
          <img
            src="/5.5M.png"
            alt="Video 1"
            className="w-full h-full object-cover transition-transform duration-300 ease-in-out transform hover:scale-105"
          />
        </div>
      </a>
    </div>

    {/* Video 2 */}
    <div className="relative overflow-hidden rounded-lg shadow-lg">
      <a
        href="https://www.instagram.com/reel/CvaKtRQARr0/"
        target="_blank"
        rel="noopener noreferrer"
        className="block"
      >
        <div className="rounded-lg overflow-hidden">
          <img
            src="/3.6.png"
            alt="Video 2"
            className="w-full h-full object-cover transition-transform duration-300 ease-in-out transform hover:scale-105"
          />
        </div>
      </a>
    </div>

    {/* Video 3 */}
    <div className="relative overflow-hidden rounded-lg shadow-lg">
      <a
        href="https://youtube.com/shorts/ZcboBHn-lNk?si=f3zthBR6R_TVTbWg"
        target="_blank"
        rel="noopener noreferrer"
        className="block"
      >
        <div className="rounded-lg overflow-hidden">
          <img
            src="/10M.png"  
            alt="Video 3"
            className="w-full h-full object-cover transition-transform duration-300 ease-in-out transform hover:scale-105"
          />
        </div>
      </a>
    </div>

    {/* Video 4 */}
    <div className="relative overflow-hidden rounded-lg shadow-lg">
      <a
        href="https://www.instagram.com/reel/CukHO7tAfTK/"
        target="_blank"
        rel="noopener noreferrer"
        className="block"
      >
        <div className="rounded-lg overflow-hidden">
          <img
            src="/4.6.png"
            alt="Video 4"
            className="w-full h-full object-cover transition-transform duration-300 ease-in-out transform hover:scale-105"
          />
        </div>
      </a>
    </div>
  </div>
</div>


        {/* Music Video Section */}
       {/* Music Video Section */}
<div className="mt-16 text-center">
  <h2 className="text-2xl md:text-3xl font-bold mb-6">Music Video</h2>
  <div className="max-w-md sm:max-w-lg mx-auto shadow-lg rounded-lg overflow-hidden">
    <div className="p-1">
      <video
        
        muted
        loop
        controls
        className="w-full h-auto object-cover rounded-lg"
      >
        <source src="music.mp4" type="video/mp4" />
        Your browser does not support the video tag.
      </video>
    </div>
  </div>
</div>





        {/* Podcast Section */}
        <div className="mt-16">
          <h2 className="text-2xl md:text-3xl font-bold mb-6">Podcast</h2>
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
            <iframe
              className="w-full rounded-lg shadow-lg"
              height="315"
              src="https://www.youtube.com/embed/DJN53lqaLQk"
              title="Podcast 1"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
              allowFullScreen
            ></iframe>
            <iframe
              className="w-full rounded-lg shadow-lg"
              height="315"
              src="https://www.youtube.com/embed/YHU7NVFrpI4"
              title="Podcast 2"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
              allowFullScreen
            ></iframe>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Portfolio;
